*{
    margin: 0px;
    padding: 0px;
    list-style:none;
    text-decoration: none;
}
header{
    background-color:#F5F5F5;
}
.promoHeader{
    display: none;
    height: 60px;
    background-color: #F5F5F5;
    align-items: center;
    justify-content: center;
}
.headerArticle{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    margin: 0 auto;
}
.favoriteHover{
    color:red;
}
.headerNav{
    width: 100%;
}
.headerUl{
    height: 50px;
    width: 100%;
    background-color:#DDA0DD;
    display: flex;
    align-items: center;
    justify-content: center;
    gap:50px;
}
.HeaderInfos{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 70%;
    margin:0 auto;
    height: 70px;
}

.containerInfo{
    display: block;
    width: 100%;
    background-color: #F5F5F5;
}
.headerUl li {
    font-weight: 530;
}
.MinhaLogo{
    margin-left: 10px;
    font-size: 1.5em;
    font-weight: bold;
    width: 30%;
}
.hamburguer{
    font-size: 1.9em;
    margin-right: 10px;
    display: none;
}
.infos{
    width:100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.formInput{
    width: 700px;
    height: 30px;
}
.containerInput{
    width: 600px;
    position: relative;
}
.containerInput input{
    border:none;
    outline: none;
    height: 35px;
    width: 100%;
    position: relative;
    border-radius: 10px;
}
.buttonPesquisar {
    border:none;
    outline: none;
    height: 35px;
    position: absolute;   
    top: 0; 
    right: 0;
    box-sizing: border-box;
    padding: 5px;
    border-radius: 0px 10px 10px 0px;
  }
  .containerPerfil{
      display: flex;
      align-items: center;
      gap:20px
  }
  .carrinho{
      position: relative;
      font-size:2.8em;
  }

  .minhaconta{
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      height: 60px;
      gap:10px;
  }
  .iconMinhaconta{
      margin-top: 5px;
      font-size: 2.1em;
  }
 .textMinhaConta{
     margin-top:5px;
 }
  .pLogin{
    font-size:0.9em;
  }
  .pLoginAcess{
    font-size:0.8em;
  }
  .containerQtdCarrinho{
      background-color: rgb(223, 221, 221);
      top:0px;
      right: -15px;
      height: 25px;
      width: 25px;
      border-radius: 50%;
      position: absolute;
      font-family: Optima, sans-serif;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size:0.35em;
  }
  .qtdCarrinho{
      font-family: Optima, sans-serif;
       text-align: center;
      font-size: 0.3em;
  }
  .containerInfoMobile{
      display: flex;
      flex-direction: row;
  }

  .perfilLogado{
      height: 50px;
      width: 50px;
      background-color: blue;
      border-radius: 50%;
  }
  .favoritos{
      margin-top:15px;
      font-size: 2.4em;
  }
  

  @media (max-width: 767.98px) { 

    .hamburguer{
        display: block;
    }
    .containerInfo{
        display: block;
        top:70px;
        position: absolute;
        z-index: 10000;
    }
   
     .containerInfoOff{
        display: none;
    }
    .headerNav{
        display: none;
    }

    .HeaderInfos{
        display: flex;
        align-items: auto;
        justify-content: space-between;
        width: 100%;
        margin:0 auto;
        height: 70px;
        position: relative;
    }
    .infos{
        width:100%;
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    .formInput{
        width: 100%;
        height: 30px;
    }
    .containerInput{
        margin:10px 0px;
        width: 100%;
        position: relative;
    }
    .containerInput input{
        border:none;
        outline: none;
        height: 35px;
        width: 100%;
        position: relative;
        border-radius: 10px;
    }
    .MinhaLogo{
        font-size: 1.5em;
        font-weight: bold;
        width: auto;
    }
    form{
        width: 100%;
    }
    .containerPerfil{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap:20px
    }
    .minhaconta{
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        height: 60px;
        gap:10px;
    }
    .containerInfoMobile{
        display: flex;
        flex-direction: row;
    }
    .abrir{
        display: none;
    }
   }