.carouselBanner{
    width: 70%;
    margin:0 auto ;
    height: 300px;
    background-color: red;
    position: relative;
    display: flex;
    overflow: hidden;
}
.divBanner{
    display: flex;
    transition: transform 2s;
}
.item{
flex-shrink: 0;
 width: 100%;
 height: 100%;
}
.btnNext{
    position: absolute;
    top:49%;
    right: 0px;
}
.btnPrev{
    position: absolute;
    top:49%;
    left: 0px;
}

@media (max-width: 767.98px) {

    .carouselBanner{
        width: 100%;
        margin:0 auto ;
        height: 120px;
        background-color: red;
        position: relative;
        display: flex;
        overflow: hidden;
    }
    .divBanner{
        display: flex;
        transition: transform 2s;
    }
    .item{
    flex-shrink: 0;
     width: 100%;
     height: 100%;
    }
    .btnNext{
        position: absolute;
        top:49%;
        right: 0px;
    }
    .btnPrev{
        position: absolute;
        top:49%;
        left: 0px;
    }


}
