.containerGeral{
    width: 100%;
    margin:10px auto;
    background-color: #FFFFFF;
}
.containerProducts{
    width: 70%;
    margin:0 auto;
}
.containerProdutosProcurados{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: flex-start;
    gap:5.5px;
    background-color: white;
}
.box_product{
    width: 16.3%;
    height: 250px;
}
.fotoProduto{
 height: 150px;
 background-color: white;
 display: flex;
 justify-content: center;
 align-items: center;
}

.fotoProduto img{
    height: 100%;
   }
.nomeProduto{
    text-align: center;
    display: flex;
    align-items: center;
    justify-content:center;
    height: 25px;
}
.valorProduto{
    text-align: center;
    height: 25px;
    font-size: 1.2em;
}
.parcelamentoProduto{
    text-align: center;
    height: 20px;
    font-size:0.8em;
}
.buttonComprarProduto{
    background-color:#EE82EE;
    height: 30px;
    width: 100%;
    border:none;
}
.banner{
    margin-top:10px;
    max-height: 305px;
}
.banner img{
    width: 100%;
    object-fit: cover;
}


@media (max-width: 767.98px) {

    .containerGeral{
        width: 100%;
        margin:10px auto;
        background-color: #FFFFFF;
    }
    .containerProducts{
        width: 100%;
        margin:0 auto;
    }
    .containerProdutosProcurados{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: flex-start;
        gap:5.5px;
        background-color: white;
    }
    .box_product{
        width: 48%;
        height: 250px;
    }
    .fotoProduto{
     height: 150px;
     background-color: white;
     display: flex;
     justify-content: center;
     align-items: center;
    }
    
    .fotoProduto img{
        height: 100%;
       }
    .nomeProduto{
        text-align: center;
        display: flex;
        align-items: center;
        justify-content:center;
        height: 25px;
    }
    .valorProduto{
        text-align: center;
        height: 25px;
        font-size: 1.2em;
    }
    .parcelamentoProduto{
        text-align: center;
        height: 20px;
        font-size:0.8em;
    }
    .buttonComprarProduto{
        background-color:#EE82EE;
        height: 30px;
        width: 100%;
        border:none;
    }
    .banner{
        margin-top:10px;
        height: 80px;
    }
    .banner img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

}